.list {
    width: 320px;
    margin: 0;
    padding: 0;
}

.item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.text {
    margin-right: 15px;
}

.btnDelete {
    height: 20px;
    box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.75);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 250ms linear;
}

.btnDelete:hover,
.btnDelete:focus {
    background-color: #b5ddf2;
}